.wrapForm {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.login {
  background: -webkit-radial-gradient(bottom center, 200% 100%, #043091, #03246d, #02194b, #000);
  background: radial-gradient(200% 100% at bottom center, #043091, #03246d, #02194b, #000);
  background: -webkit-radial-gradient(top center, 220% 105%, #010613 20%, #010c24 40%, #021742 65%, #03194b);
  background: radial-gradient(220% 105% at top center, #010613 20%, #010c24 40%, #021742 65%, #03194b);
  background-attachment: fixed;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.loginWrap {
  position: absolute;
  background: #fff;
  width: 350px;
  height: 500px;
  left: 50%;
  top: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  opacity: 0.9;
  box-shadow: 2px 9px 16px 1px #010613;
}
.wrapForm .login-form {
  max-width: 270px;
  margin: 0 auto;
  margin-top: 40px;
}
.wrapForm .login-form-forgot {
  float: right;
}
.wrapForm .login-form-button {
  width: 100%;
}
.loginTitle {
  width: 100%;
  height: 30px;
  margin: 0 auto;
  text-align: center;
  margin-top: 70px;
}

